<template>
	<div class="outer" :class="classList[classIndex%3]" @click="classIndex++">
		<div class="avatar" :style="{'backgroundImage':`url(${status.avatar})`,'borderColor':color}" />
		<div class="question-bar">
			<div class="username shadow" :style="{'backgroundColor':color}"><span>{{status.nickName}} 问道：</span>
				<div class="remain-time">剩余时间：{{remain}}</div>
			</div>
			<div class="question shadow"><span>{{status.question}}</span></div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from "vue";
import { wsevent } from "@front/api/wsbus";
import { isOnline } from "@front/api/server";
export default defineComponent({
	name: "danmakuQuestion",
	cname: "弹幕提问",
	icon: "self-question",
	description: "送出香蕉或礼物后，可在主席台提问，问题一直维持到冷却时间结束",
	configurations: {
		width: 400,
		height: 300,
		resizable: true,
		multiple: false,
		liveOnly: true
	},
	data() {
		return {
			status: {
				nickName: "林梦仙",
				avatar:
					"https://tx-free-imgs.acfun.cn/content/2020_11_9/1.604895600393797E9.png?imageMogr2/auto-orient/format/webp/quality/75!/ignore-error/1",
				question: "我是猪？",
				remainTime: 100000
			},
			settings: {
				ownerInfinity: true
			},
			active: true,
			colorList: ["#b5d7ef"],
			colorIndex: 0,
			classIndex: 0,
			classList: ["", "inactive", "active"]
		};
	},
	computed: {
		color() {
			return this.colorList[this.colorIndex];
		},
		remain() {
			const time = this.status.remainTime / 1000;
			return `${parseInt(time / 60)}:${time % 60}`;
		}
	},
	mounted() {
		this.registerOBS();
		wsevent.on("voteUpdate", e => {
			return 1;
		});
	},
	beforeUnmount() {
		clearTimeout(this.timer);
	},
	methods: {
		registerOBS() {
			clearTimeout(this.timer);
			if (!isOnline()) {
				this.$store.dispatch("startServe").then(() => {
					wsevent.register(`obs-${this.$options.name}`);
				});
			}
			this.timer = setTimeout(() => {
				this.registerOBS();
			}, 3000);
		}
	}
});
</script>

<style scoped lang='scss'>
* {
	box-sizing: border-box;
}
.center-column {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.center-column > * {
	margin-bottom: 8px;
}
@font-face {
	font-family: "站酷快乐体";
	src: url("/fonts/站酷快乐体.ttf") format("truetype");
}
.shadow {
	text-shadow: rgb(0, 0, 0) -0.8px -0.8px 0px, rgb(0, 0, 0) 0px -0.8px 0px,
		rgb(0, 0, 0) 0.8px -0.8px 0px, rgb(0, 0, 0) 0.8px 0px 0px,
		rgb(0, 0, 0) 0.8px 0.8px 0px, rgb(0, 0, 0) 0px 0.8px 0px,
		rgb(0, 0, 0) -0.8px 0.8px 0px, rgb(0, 0, 0) -0.8px 0px 0px;
	color: white;
	font-family: 站酷快乐体;
}
.outer {
	position: relative;
}
.question-bar {
	position: absolute;
	width: calc(100% - 50px);
	top: 0px;
	left: 0px;
	z-index: -1;
	margin-left: 50px;

	.username {
		width: 100%;
		padding: 5px 0px;
		padding-left: 55px;
		font-size: 18px;
		white-space: nowrap;
		border-radius: 0 20px 0 0;
		overflow: hidden;
		position: relative;
	}
	.question {
		padding: 5px 0px;
		padding-left: 55px;
		background-color: white;
		overflow: hidden;
	}
}
.avatar {
	width: 100px;
	height: 100px;
	opacity: 0;
	background-size: 100% 100%;
	border-radius: 100%;
	border-width: 7px;
	border-style: solid;
	position: relative;
	animation: avatar 2s ease forwards;
}
.remain-time {
	position: absolute;
	white-space: nowrap;
	right: 12px;
	top: 6px;
	animation: time 2s ease forwards;
}
.active {
	.avatar {
		animation: avatar 2s ease forwards;
	}
	.username,
	.question {
		animation: bar 2s ease forwards;
	}
	.remain-time,
	.question-bar span {
		animation: time 2s ease forwards;
	}
}
.inactive {
	.avatar {
		animation: avatar 2s ease forwards reverse;
	}
	.username,
	.question {
		animation: bar 2s ease forwards reverse;
	}
	.remain-time,
	.question-bar span {
		animation: time 2s ease forwards reverse;
	}
}
@keyframes avatar {
	0% {
		opacity: 0;
		transform: scale(20%) rotate(-180deg);
	}
	40% {
		opacity: 1;
	}
	50% {
		transform: scale(100%) rotate(0deg);
	}
	100% {
		opacity: 1;
		transform: scale(100%);
	}
}
@keyframes bar {
	0% {
		padding: 0px;
		width: 0%;
		white-space: nowrap;
	}
	40% {
		padding: 0px;
		width: 0%;
		white-space: nowrap;
	}
	100% {
		width: 100%;
	}
}
@keyframes time {
	0% {
		opacity: 0;
	}
	60% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
</style>
